import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { environment } from "../../environments/environment";
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection } from '@angular/fire/firestore';

const { baseUrl } = environment
@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private http: HttpClient, public afs: AngularFirestore,) { }
  getVMList() {
    let headers = new HttpHeaders().set('Access-Control-Allow-Origin', "*")
    return this.http.get<any>(`https://us-central1-jupyter-run-dev.cloudfunctions.net/VirtualMachineList?uid=${JSON.parse(localStorage.getItem('user_uid'))}`, {
      headers: headers
    });
  }
  stopVM(name: string, zone: string) {
    let headers = new HttpHeaders().set('Access-Control-Allow-Origin', "*")
    return this.http.get<any>(`https://us-central1-jupyter-run-dev.cloudfunctions.net/stopVirtualMachine?uid=${JSON.parse(localStorage.getItem('user_uid'))}&vm_name=${name}&vm_zone=${zone}`, {
      headers: headers
    });
  }
  startVM(name: string, zone: string) {
    let headers = new HttpHeaders().set('Access-Control-Allow-Origin', "*")
    return this.http.get<any>(`https://us-central1-jupyter-run-dev.cloudfunctions.net/startVirtualMachine?uid=${JSON.parse(localStorage.getItem('user_uid'))}&vm_name=${name}&vm_zone=${zone}`, {
      headers: headers
    });
  }
  deleteVM(name: string, zone: string) {
    let headers = new HttpHeaders().set('Access-Control-Allow-Origin', "*")
    this.afs.collection(`users/${JSON.parse(localStorage.getItem('user_uid'))}/VMs`).doc(name).delete()

    return this.http.get<any>(`https://us-central1-jupyter-run-dev.cloudfunctions.net/deleteVirtualMachine?uid=${JSON.parse(localStorage.getItem('user_uid'))}&vm_name=${name}&vm_zone=${zone}`, {
      headers: headers
    });
  }
  createVM({ name, vmType, jupyterPassword, region }) {
    const uid = JSON.parse(localStorage.getItem('user_uid'))
    const params = new HttpParams()
      .append('uid', uid)
      .append('name', name)
      .append('machine_type', vmType)
      .append('password', jupyterPassword)
      .append('region', region)



    return this.http.get(baseUrl + '/createVirtualMachine', { params });
  }

  addVMIdToFirebase({ instance_id, ip }, name: string) {
    const data = {
      instance_id,
      ip,
    }
    const userRef: AngularFirestoreDocument<any> = this.afs.collection(`users/${JSON.parse(localStorage.getItem('user_uid'))}/VMs`).doc(name)
    // doc(`users/${JSON.parse(localStorage.getItem('user_uid'))}/${name}`);

    return userRef.set(data, { merge: true })
  }

  getRegionsAndMachineTypes() {
    let headers = new HttpHeaders().set('Access-Control-Allow-Origin', "*")
    return this.http.get<any>(`https://us-central1-jupyter-run-dev.cloudfunctions.net/RegionsAndMachineTypes?uid=${JSON.parse(localStorage.getItem('user_uid'))}`, {
      headers: headers
    });
  }
}
