import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { auth } from 'firebase';
import { DataService } from 'src/app/services/data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-notebook-card',
  templateUrl: './notebook-card.component.html',
  styleUrls: ['./notebook-card.component.scss'],
})
export class NotebookCardComponent implements OnInit {
  @Input() vm_title: string;
  @Input() vm_zone: string;
  @Input() vm_ip: string;
  @Input() isRuning: boolean;
  @Output() onRemoveVM: EventEmitter<string> = new EventEmitter<string>();
  isStarted: boolean = false
  constructor(private data: DataService) { }



  ngOnInit() {
    if (localStorage.getItem('progress') == this.vm_title) {
      this.isStarted = true
    } else {
      this.isStarted = false

    }
  }

  openAddress(ip: string) {
    window.open("http://" + ip, "_blank")
  }
  startVM() {
    this.isStarted = true

    this.data.startVM(this.vm_title, this.vm_zone).subscribe(data => {
      if (data['status'] == 200)
        this.isRuning = true
      this.isStarted = false

    })
  }

  stopVM() {
    this.isStarted = true

    this.data.stopVM(this.vm_title, this.vm_zone).subscribe(data => {
      if (data['status'] == 200)
        this.isRuning = false
      this.isStarted = false

    })
  }
  deleteVM() {
    localStorage.setItem("progress", this.vm_title)
    if (localStorage.getItem("progress") != undefined) {
      this.isStarted = true
    }
    this.data.deleteVM(this.vm_title, this.vm_zone).subscribe(data => {

      if (data['status'] == 200)
        setTimeout(() => {

          this.onRemoveVM.emit(this.vm_title)

        }, 20000)

    })
  }
}
